/*----------------------------------
	Common HTML elements and more for easier usage
------------------------------------*/
html, html a, body {
  -webkit-font-smoothing: antialiased;
}

html, body {
  overflow-x: hidden;
}

html {
  font-size: 16px;
  -webkit-tap-highlight-color: transparent;
}

body {
  font-size: 1rem;
  font-family: Avenir, sans-serif;
  color: #232323;
  line-height: 1.4;
  background-color: #fff;
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  font-weight: 500;
  font-family: Avenir, sans-serif;
  line-height: 1.2;
  color: #363d46;
  margin-top: 0;
  margin-bottom: 0.75rem;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: #363d46;
  transition-duration: 200ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

h1 a:focus, h1 a:hover,
h2 a:focus,
h2 a:hover,
h3 a:focus,
h3 a:hover,
h4 a:focus,
h4 a:hover,
h5 a:focus,
h5 a:hover,
h6 a:focus,
h6 a:hover {
  color: #4a4ace;
  transition-duration: 200ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

em,
li {
  font-size: 0.8125rem;
  font-weight: 400;
  font-family: Roboto, sans-serif;
  color: #363d46;
}

em > a,
li > a {
  color: #363d46;
}

em > a:hover,
li > a:hover {
  color: #4a4ace;
}

small {
  color: #8e98a7;
}

label {
  font-size: 0.8125rem;
  font-weight: 500;
  color: #363d46;
  margin-bottom: .25rem;
}

i {
  line-height: 1;
}

/* Paragraph */
p {
  font-size: 1rem;
  font-weight: 400;
  font-family: Roboto, sans-serif;
  color: #8e98a7;
  line-height: 1.4;
  margin-top: 0;
  margin-bottom: 1rem;
}

/* Links */
a {
  font-size: 0.9375rem;
  font-weight: 400;
  font-family: Roboto, sans-serif;
  color: #4a4ace;
  text-decoration: none;
}

a:focus, a:hover {
  color: #3434c0;
  text-decoration: none;
}

a:focus {
  outline: none;
}

/* Link v1 */
.s-link-v1 {
  position: relative;
  font-size: 0.9375rem;
}

.s-link-v1:before, .s-link-v1:after {
  position: absolute;
  right: 0;
  bottom: -0.625rem;
  width: 0;
  height: 0.1rem;
  content: " ";
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.s-link-v1:before {
  background: #fff43e;
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
}

.s-link-v1:after {
  background: #4a4ace;
}

.s-link-v1:hover:before, .s-link-v1:hover:after {
  left: 0;
  width: 100%;
}

/* SVG */
.s-svg {
  width: 100%;
}

.s-svg * {
  fill: none;
  stroke: currentColor;
}

/*----------------------------------
  Selection
------------------------------------*/
::selection {
  color: #fff;
  background: #4a4ace;
  text-shadow: none;
}

::-webkit-selection {
  color: #fff;
  background: #4a4ace;
  text-shadow: none;
}

:active,
:focus {
  outline: none;
}

/*----------------------------------
  Button v1
------------------------------------*/
.s-btn-v1 {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  border: none;
  white-space: nowrap;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.s-btn-v1:focus, .s-btn-v1:hover {
  text-decoration: none;
}

.s-btn-v1.focus {
  text-decoration: none;
}

.s-btn-v1:active, .s-btn-v1.active {
  background-image: none;
  outline: 0;
}

.s-btn-v1.disabled, .s-btn-v1:disabled,
fieldset[disabled] .s-btn-v1 {
  cursor: not-allowed;
  opacity: .65;
}

/* Styles
------------------------------ */
.s-btn-v1--white-bg {
  color: #363d46;
  background: #fff;
}

.s-btn-v1--white-bg:focus, .s-btn-v1--white-bg:hover {
  color: #4a4ace;
  background: #fff;
}

.s-btn-v1--white-brd {
  color: #fff;
  background: transparent;
  border-width: 0.125rem;
  border-style: solid;
  border-color: #fff;
}

.s-btn-v1--white-brd:focus, .s-btn-v1--white-brd:hover {
  color: #4a4ace;
  background: #fff;
  border-color: #fff;
}

.s-btn-v1--primary-brd {
  color: #4a4ace;
  background: transparent;
  border-width: 0.125rem;
  border-style: solid;
  border-color: #4a4ace;
}

.s-btn-v1--primary-brd:focus, .s-btn-v1--primary-brd:hover {
  color: #fff;
  background: #4a4ace;
  border-color: #4a4ace;
}

.s-btn-v1--primary-bg {
  color: #fff;
  background: #4a4ace;
  border-width: 0.125rem;
  border-style: solid;
  border-color: #4a4ace;
}

.s-btn-v1--primary-bg:focus, .s-btn-v1--primary-bg:hover {
  color: #fff;
  background: #4a4ace;
}

/* Primary Background Style
------------------------------ */
/* Primary v2 with After Pseudo Element */
.s-btn-v1--primary-bg-after {
  position: relative;
  text-align: center;
}

.s-btn-v1--primary-bg-after__divider:before {
  position: absolute;
  top: -3.75rem;
  left: 50%;
  width: 0.125rem;
  height: 2.5rem;
  background: #4a4ace;
  content: " ";
  margin-left: -.0625rem;
}

@media (max-width: 47.9em) {
  .s-btn-v1--primary-bg-after__divider-no:before {
    display: none;
  }
}

.s-btn-v1--primary-bg-after__bubbles {
  position: absolute;
  top: -0.8125rem;
  left: 50%;
  width: 1.5rem;
  height: 1.5rem;
  z-index: -1;
  background: #4a4ace;
  border-radius: 50%;
  margin-left: -.75rem;
}

.s-btn-v1--primary-bg-after__bubbles:before, .s-btn-v1--primary-bg-after__bubbles:after {
  position: absolute;
  top: 0.438rem;
  left: 50%;
  width: 1.5rem;
  height: 1.5rem;
  background: #4a4ace;
  border-radius: 50%;
  content: " ";
}

.s-btn-v1--primary-bg-after__bubbles:before {
  margin-left: -1.5rem;
}

.s-btn-v1--primary-bg-after__bubbles:after {
  margin-right: -1.5rem;
}

.s-btn-v1--primary-bg-after__circles:before, .s-btn-v1--primary-bg-after__circles:after {
  border-radius: 50%;
  content: " ";
}

.s-btn-v1--primary-bg-after__circles:before {
  position: absolute;
  top: -2.1875rem;
  left: 50%;
  width: 0.5rem;
  height: 0.5rem;
  background: #4a4ace;
  margin-left: -1.125rem;
}

.s-btn-v1--primary-bg-after__circles:after {
  position: absolute;
  top: -1.5rem;
  left: 50%;
  width: 0.75rem;
  height: 0.75rem;
  border: 0.1875rem solid #4a4ace;
  margin-left: .6875rem;
}

/*----------------------------------
  Clients v1
------------------------------------*/
.s-clients-v1 {
  display: block;
  width: 4.995rem;
  height: auto;
  opacity: .7;
  margin-left: auto;
  margin-right: auto;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.s-clients-v1:hover {
  opacity: 1;
  cursor: pointer;
}

/*----------------------------------
  Contact v1
------------------------------------*/
.s-contact-v1__success,
.s-contact-v1__error {
  position: absolute;
  top: 50%;
  left: 50%;
  display: none;
  text-align: center;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
}

.s-contact-v1__success span,
.s-contact-v1__error span {
  display: block;
}

/*----------------------------------
  Form Input v1
------------------------------------*/
.s-form-input-v1 {
  position: relative;
  z-index: 1;
  display: inline-block;
  width: 100%;
  overflow: hidden;
}

.s-form-input-v1__field {
  height: 3.15rem;
  font-size: 0.875rem;
  color: #8e98a7;
  background: #fff;
  border: 0.11rem solid rgba(142, 152, 167, 0.4);
  box-shadow: none;
  padding: .4375rem 1.5rem;
}

.s-form-input-v1__field::-webkit-input-placeholder {
  color: #8e98a7;
}

.s-form-input-v1__field::-moz-placeholder {
  color: #8e98a7;
}

.s-form-input-v1__field:-ms-input-placeholder {
  color: #8e98a7;
}

.s-form-input-v1__field::placeholder {
  color: #8e98a7;
}

.s-form-input-v1__field:focus {
  background: #fff;
  border-color: #4a4ace;
  box-shadow: none;
  color: #8e98a7;
}

.s-form-input-v1__field:focus::-webkit-input-placeholder {
  color: #8e98a7;
}

.s-form-input-v1__field:focus::-moz-placeholder {
  color: #8e98a7;
}

.s-form-input-v1__field:focus:-ms-input-placeholder {
  color: #8e98a7;
}

.s-form-input-v1__field:focus::placeholder {
  color: #8e98a7;
}

.s-form-input-v1__field:focus ~ .s-form-input-v1__label {
  font-size: 0.6875rem;
  color: #4a4ace;
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s;
  -webkit-transform: translate3d(1.3rem, 0, 0);
  transform: translate3d(1.3rem, 0, 0);
}

.s-form-input-v1__label {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 0.875rem;
  font-weight: 400;
  color: #8e98a7;
  pointer-events: none;
  padding: .15rem .25rem;
  margin-bottom: 0;
  -webkit-transform: translate3d(1.3rem, 0.875rem, 0);
  transform: translate3d(1.3rem, 0.875rem, 0);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.s-form-input-v1 .-is-active ~ .s-form-input-v1__label {
  font-size: 0.6875rem;
  color: #4a4ace;
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s;
  -webkit-transform: translate3d(1.3rem, 0, 0);
  transform: translate3d(1.3rem, 0, 0);
}

/* Error Message */
.s-form-input-v1 label.error {
  font-size: 0.75rem;
  font-style: italic;
  color: #ef3e4a;
  padding-left: 1.5rem;
}

.input-group .s-form-input-v1__field {
  position: static;
}

/*----------------------------------
  Theme Icons v1
------------------------------------*/
.s-icon-v1 {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.s-icon-v1:focus, .s-icon-v1:hover {
  text-decoration: none;
}

/* White Border Style
------------------------------ */
.s-icon-v1--white-opacity-light {
  color: rgba(255, 255, 255, 0.5);
  background: transparent;
  line-height: 1.4;
}

.s-icon-v1--white-opacity-light:focus, .s-icon-v1--white-opacity-light:hover {
  color: #4a4ace;
  background: #fff;
}

.s-icon-v1--white-brd {
  color: #fff;
  background: transparent;
  border-width: 0.125rem;
  border-style: solid;
  border-color: #fff;
  line-height: 1.4;
}

.s-icon-v1--white-brd:focus, .s-icon-v1--white-brd:hover {
  color: #4a4ace;
  background: #fff;
  border-color: #fff;
}

.s-icon-v1--white-bg {
  color: #4a4ace;
  background: #fff;
  line-height: 1.4;
}

.s-icon-v1--white-bg:focus, .s-icon-v1--white-bg:hover {
  color: #fff;
  background: #4a4ace;
}

/* Icon v2 White with After Pseudo Element */
.s-icon-v1--white-brd-after {
  position: relative;
}

.s-icon-v1--white-brd-after:after {
  position: absolute;
  left: 50%;
  bottom: -2rem;
  width: 0.125rem;
  height: 1.25rem;
  background: #fff;
  content: " ";
  margin-left: -.0625rem;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

/* Icon v2 Primary Background Style
------------------------------ */
.s-icon-v1--primary-bg {
  color: #fff;
  background: #4a4ace;
  line-height: 1.6;
}

/* Icon V1 Primary with After Pseudo Element */
.s-icon-v1--primary-bg-after {
  position: relative;
}

.s-icon-v1--primary-bg-after:after {
  position: absolute;
  left: 50%;
  bottom: -2.5rem;
  width: 0.125rem;
  height: 1.875rem;
  background: #4a4ace;
  content: " ";
  margin-left: -.0625rem;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

/* Icon v2 Primary Border Style
------------------------------ */
.s-icon-v1--primary-brd {
  color: #4a4ace;
  background: transparent;
  border-width: 0.125rem;
  border-style: solid;
  border-color: #4a4ace;
  line-height: 1.4;
}

.s-icon-v1--primary-brd:focus, .s-icon-v1--primary-brd:hover {
  color: #fff;
  background: #4a4ace;
  border-color: #4a4ace;
}

/*----------------------------------
  Iconz
------------------------------------*/
.s-iconz {
  margin-bottom: 3.125rem;
}

.s-iconz:before, .s-iconz:after {
  content: " ";
  display: table;
}

.s-iconz:after {
  clear: both;
}

.s-iconz__item {
  width: 9.6%;
  height: 6.25rem;
  float: left;
  text-align: center;
  border-radius: .25rem;
  padding: 1.0625rem .6875rem;
  transition-duration: 200ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

@media (max-width: 61.9em) {
  .s-iconz__item {
    width: 16%;
  }
}

@media (max-width: 47.9em) {
  .s-iconz__item {
    width: 20%;
  }
}

@media (max-width: 33.9em) {
  .s-iconz__item {
    width: 25%;
  }
}

.s-iconz__item:hover {
  background: #fff;
  box-shadow: 0 0 1.875rem 0 rgba(35, 35, 35, 0.03);
  cursor: pointer;
}

.s-iconz__item:hover .s-iconz__size,
.s-iconz__item:hover .s-iconz__text {
  color: #4a4ace;
}

.s-iconz__size {
  display: block;
  font-size: 1.875rem;
  color: #232323;
  margin-bottom: .5rem;
}

.s-iconz__text {
  display: block;
  font-size: 0.6875rem;
  line-height: 1.3;
  color: #232323;
  background: transparent;
  border-radius: 0;
  padding: 0;
}

/*----------------------------------
  Video Popup v1
------------------------------------*/
.s-video-popup-v1 {
  width: 3.125rem;
  z-index: 1;
  text-align: center;
  display: block;
  margin: 0 auto;
}

/*----------------------------------
	Counter v1
------------------------------------*/
.s-counter-v1 {
  position: relative;
}

.s-counter-v1:before {
  position: absolute;
  top: 50%;
  left: 90%;
  width: 20%;
  height: 0.0625rem;
  background: rgba(35, 35, 35, 0.2);
  content: " ";
  margin-top: -.03125rem;
}

@media (max-width: 61.9em) {
  .s-counter-v1:before {
    display: none;
  }
}

.s-counter-v1:last-child:before {
  display: none;
}

/*----------------------------------
  Features v1
------------------------------------*/
/* Power Icon */
.s-features-v1__icon {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  width: 3.75rem;
  height: 3.75rem;
  font-size: 1.625rem;
  padding: 1.0625rem;
}

.s-features-v1__icon-primary {
  color: #fff;
  background: #4a4ace;
  box-shadow: 0 0 0 0.1875rem rgba(132, 120, 211, 0.6);
}

/* Divider */
.s-features-v1__divider {
  position: relative;
}

.s-features-v1__divider:before {
  position: absolute;
  top: 0;
  left: 50%;
  bottom: 0;
  display: block;
  border-left: 0.0625rem solid #c8c8c8;
  margin-left: -.03125rem;
  content: " ";
}

/* Media */
.s-features-v1__media {
  position: relative;
  text-align: center;
  padding: 1.875rem;
  border-radius: .25rem;
}

.s-features-v1__media:before, .s-features-v1__media:after {
  border-radius: .25rem;
  content: " ";
}

@media (max-width: 47.9em) {
  .s-features-v1__media:before, .s-features-v1__media:after {
    display: none;
  }
}

.s-features-v1__media:before {
  position: absolute;
  top: 5%;
  width: 100%;
  height: 90%;
  z-index: -1;
}

.s-features-v1__media:after {
  position: absolute;
  top: 10%;
  width: 100%;
  height: 80%;
  z-index: -2;
}

.s-features-v1__media--left:before {
  left: -5%;
}

.s-features-v1__media--left:after {
  left: -10%;
}

.s-features-v1__media--right:before {
  right: -5%;
}

.s-features-v1__media--right:after {
  right: -10%;
}

.s-features-v1__media-mediumpurple {
  background: #bdb6ea;
}

.s-features-v1__media-mediumpurple:before {
  background: #aeaee9;
}

.s-features-v1__media-mediumpurple:after {
  background: #c2c2ef;
}

.s-features-v1__media-powderblue {
  background: #abcade;
}

.s-features-v1__media-powderblue:before {
  background: #e2edf4;
}

.s-features-v1__media-powderblue:after {
  background: #f1f6f9;
}

/*----------------------------------
  Promo Block v1
------------------------------------*/
.s-promo-block-v1__center {
  position: relative;
  display: block;
  max-width: 26.5625rem;
  margin: 0 auto 4.37rem;
}

@media (max-width: 33.9em) {
  .s-promo-block-v1__center {
    max-width: 18.75rem;
  }
}

.s-promo-block-v1__img {
  position: absolute;
  top: -2.8125rem;
  left: -2.8125rem;
  max-width: 18.75rem;
}

@media (max-width: 33.9em) {
  .s-promo-block-v1__img {
    top: -1.25rem;
    left: -1.875rem;
    max-width: 13.4375rem;
  }
}

/*----------------------------------
  Promo Mockup v1
------------------------------------*/
.s-promo-mockup-v1__img {
  position: absolute;
  bottom: 1.25rem;
}

@media (min-width: 34em) {
  .s-promo-mockup-v1__img {
    position: absolute;
    top: 50%;
    right: 0;
    bottom: 0;
    -webkit-transform: translate3d(0, -50%, 0);
    transform: translate3d(0, -50%, 0);
  }
}

/*----------------------------------
  Scroll To Section v1
------------------------------------*/
.s-scroll-to-section-v1--bc {
  position: absolute;
  left: 50%;
  bottom: 1.875rem;
  margin-left: -1.5625rem;
}

/*----------------------------------
  Services v1
------------------------------------*/
.s-services-v1 .s-services-v1__hover {
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.s-services-v1:hover .s-services-v1__hover {
  color: #4a4ace;
}

/*----------------------------------
  Header v1
------------------------------------*/
/* Navbar
------------------------------ */
.s-header-v1__navbar {
  position: relative;
  z-index: 1000;
  min-height: 3.125rem;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.s-header-v1__navbar:before, .s-header-v1__navbar:after {
  content: " ";
  display: table;
}

.s-header-v1__navbar:after {
  clear: both;
}

@media (max-width: 61.9em) {
