/*----------------------------------
  Loader v1
------------------------------------*/
.s-page-loader-v1 {
  position: fixed;
  top: 0;
  z-index: 99999;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(145deg, #4a4ace 0%, #5e5ed3 100%);
  background-repeat: repeat-x;
}

.s-page-loader-v1__element {
  position: relative;
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  opacity: 1;
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
  transition-duration: 1000ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.s-page-loader-v1__loading-pulse-1, .s-page-loader-v1__loading-pulse-2, .s-page-loader-v1__loading-pulse-3 {
  position: relative;
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 50%;
  background: #fff;
  margin: 0 .12rem;
  -webkit-animation: page-loader-v2__element 1.5s ease-in-out infinite both;
  animation: page-loader-v2__element 1.5s ease-in-out infinite both;
}

.s-page-loader-v1__loading-pulse-2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

.s-page-loader-v1__loading-pulse-3 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

/* Page Load Animation When Loading Finishes */
.s-page-loader-v1.-is-animated {
  -webkit-animation: page-loader-v2__loaded 1s cubic-bezier(0.7, 0, 0.3, 1) forwards;
  animation: page-loader-v2__loaded 1s cubic-bezier(0.7, 0, 0.3, 1) forwards;
}

.s-page-loader-v1.-is-animated .s-page-loader-v1__element {
  -webkit-transform: scale(0.3, 0.3);
  transform: scale(0.3, 0.3);
  opacity: 0;
}

/* Keyframes Page Element */
@-webkit-keyframes page-loader-v2__element {
  0, 100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
  50% {
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0);
  }
}

@keyframes page-loader-v2__element {
  0, 100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
  50% {
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0);
  }
}

/* Keyframes Page Loaded */
@-webkit-keyframes page-loader-v2__loaded {
  to {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
}

@keyframes page-loader-v2__loaded {
  to {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
}

